<template>
    <div class="form">
        <input type="text" v-model="poll.title" class="title-input" /> <br />
        <input type="text" v-model="poll.description" placeholder="Opis formualrza" class="description-input" />

        <div v-for="(question, index) in allQuestions" :key="index" class="question">

            <single-choice-question v-if="question.questionType == 'SingleChoice'"
                :question="question"
                @deleteQuestion="deleteQuestion"
                @deleteChoice="deleteChoice"
                @addChoice="addChoice" />

            <multiple-choice-question v-if="question.questionType == 'MultipleChoice'"
                :question="question"
                @deleteQuestion="deleteQuestion"
                @deleteChoice="deleteChoice"
                @addChoice="addChoice" />

            <text-answer-question v-if="question.questionType == 'TextAnswer'"
                :question="question"
                @deleteQuestion="deleteQuestion" />
        </div>

        <div class="buttons">
            Dodaj pytanie: 
            <a class="btn" @click="addSingleChoiceQuestion">jednokrotny wybór</a> | 
            <a class="btn" @click="addMultipleChoiceQuestion">wielokrotny wybór</a> |
            <a class="btn" @click="addTextAnswerQuestion">odpowiedz tekstowa</a>
        </div>
        

        <div>
            <a class="btn" @click="save">Zapisz</a>
        </div>
    </div>
</template>

<script>
import SingleChoiceQuestion from './SingleChoiceQuestion.vue'
import MultipleChoiceQuestion from './MultipleChoiceQuestion.vue'
import TextAnswerQuestion from './TextAnswerQuestion.vue'

import axios from 'axios'
export default {
    name: 'Poll',
    components: {
        SingleChoiceQuestion,
        MultipleChoiceQuestion,
        TextAnswerQuestion,
    },

    data() {
        return {
            poll: {
                pollId: 0,
                title: '',
                description: '',
                questions: []
            },

            questionsToDelete: [],
            newQuestions: []
        }
    },

    mounted() {
        let url = new URL(window.location.href)

        // get id from query string
        let params = url.searchParams
        let id = parseInt(params.get('id'))

        //get poll to edit and populate poll in data object
        axios({
            method: 'get',
            url: `https://localhost:44392/polls/edit/poll/${id}`
        }).then(res => {
            this.poll.pollId = res.data.pollId;
            this.poll.title = res.data.title;
            this.poll.description = res.data.description;
            this.poll.questions = res.data.questions;
        })
    },

    computed: {
        allQuestions() {
            return this.poll.questions.concat(this.newQuestions)
        }
    },

    methods: {
        deleteQuestion(question) {
            // check if it is new quetion
            let i = this.newQuestions.findIndex(x => x == question)

            // if yes, just delete it from newQuestions array
            if (i != -1) {
                this.newQuestions.splice(i, 1)
                return
            }

            let index = this.poll.questions.indexOf(question);

            this.questionsToDelete.push(question)
            this.poll.questions.splice(index, 1)

        },

        deleteChoice(question, index) {
            question.choices.splice(index, 1)
        },

        addChoice(question) {
            let num = question.choices.length + 1
            question.choices.push("Opcja " + num)
        },

        addSingleChoiceQuestion() {
            this.newQuestions.push({
                    questionText: 'Tresc pytania',
                    questionType: 'SingleChoice',
                    choices: ['Opcja 1', 'Opcja 2', 'Opcja 3'],
                    number: 0
            })
        },

        addMultipleChoiceQuestion() {
            this.newQuestions.push({
                    questionText: 'Tresc pytania',
                    questionType: 'MultipleChoice',
                    choices: ['Opcja 1', 'Opcja 2', 'Opcja 3'],
                    number: 0
            })
        },

        addTextAnswerQuestion() {
            this.newQuestions.push({
                questionText: 'Tresc pytania',
                questionType: 'TextAnswer',
                number: 0
            })
        },

        giveQuestionANumber() {
            let counter = 1
            this.allQuestions.forEach(x => {
                x.number = counter
                counter += 1
            })
        },

        save() {
            this.giveQuestionANumber()

            // prepare request object
            let req = {
                pollId: this.poll.pollId,
                newTitle: this.poll.title,
                newDescription: this.poll.description,
                
                scQuestionsToUpdate: this.poll.questions.filter(x => x.questionType == 'SingleChoice'),
                mcQuestionsToUpdate: this.poll.questions.filter(x => x.questionType == 'MultipleChoice'),
                taQuestionsToUpdate: this.poll.questions.filter(x => x.questionType == 'TextAnswer'),

                newScQuestions: this.newQuestions.filter(x => x.questionType == 'SingleChoice'),
                newMcQuestions: this.newQuestions.filter(x => x.questionType == 'MultipleChoice'),
                newTaQuestions: this.newQuestions.filter(x => x.questionType == 'TextAnswer'),

                scQuestionsToDelete: this.questionsToDelete.filter(x => x.questionType == 'SingleChoice'),
                mcQuestionsToDelete: this.questionsToDelete.filter(x => x.questionType == 'MultipleChoice'),
                taQuestionsToDelete: this.questionsToDelete.filter(x => x.questionType == 'TextAnswer'),
            }

            axios({
                method: 'post',
                url: 'https://localhost:44392/polls/edit',
                data : req
            }).then(res => {
                if(res.status == 200) {
                    window.location.href = 'https://localhost:44392/'
                }
            }).catch(() => {
                alert("Coś poszło nie tak przy zapisywaniu formularza, spróbuj ponownie póżniej")
            })
        },
    } 
}
</script>

