<template>
    <div>
        <a @click="$emit('deleteQuestion', question)" class="x-btn delete-question-btn">&times;</a>

        <div class="question-text">
            <label>
                <input v-model="question.questionText" class="question-text-input">
            </label>
        </div>

        <div class="choices">
            <div class="choice" v-for="(choice, index) in question.choices" :key="index">
                <input type="checkbox" disabled><input v-model="question.choices[index]">
                <a @click="$emit('deleteChoice', question, index)" class="x-btn">&times;</a>
                <br />
            </div>
        </div>
        <a @click="$emit('addChoice', question)" class="add-choice-btn btn">Dodaj opcję</a>
    </div>
</template>

<script>
export default {
    name: 'MultipleChoiceQuestion',
    props: {
        question: Object
    }, 

    components: {

    }
}
</script>

